<template>
<nav>
  <a href="/addImage"  class="add">Add Image</a>
  <div id="map"></div>
  <img v-bind:src="'data:image/jpeg;base64,'+images[0].url" alt="image"/>


</nav>
</template>

<script>
import axios from 'axios'
import YandexMap from '../mapScript'

export default {

  data() {
    return {
      error: false,
      coords: [],
      images: []
    }
  },
  mounted() {
    this.$nextTick(function () {
      axios.get('/api/image?page='+0).then(response => {
        this.images=response.data.data
      }).catch((error) => {
        console.log(error.response.status)
      })

      ymaps.ready(() => {
        const myMap = new ymaps.Map('map', {
          center: [53.893009, 27.567444],
          zoom: 10
        });

        yellowCollection = new ymaps.GeoObjectCollection(null, {
          preset: 'islands#yellowIcon'
        }),
          blueCollection = new ymaps.GeoObjectCollection(null, {
            preset: 'islands#blueIcon'
          }),
          yellowCoords = [[55.73, 37.75], [55.81, 37.75]],
          blueCoords = [[55.73, 37.65], [55.81, 37.65]];

        for (var i = 0, l = yellowCoords.length; i < l; i++) {
          yellowCollection.add(new ymaps.Placemark(yellowCoords[i]));
        }
        for (var i = 0, l = blueCoords.length; i < l; i++) {
          blueCollection.add(new ymaps.Placemark(blueCoords[i]));
        }

        myMap.geoObjects.add(yellowCollection).add(blueCollection);

        //        .
        yellowCollection.events.add('click', function () { alert('   ') });
        blueCollection.events.add('click', function () { alert('   ') });

        //       .
        blueCollection.options.set('preset', 'islands#blueDotIcon');



        myMap.events.add('click', (e) => {
          if (!myMap.balloon.isOpen()) {
            this.coords = e.get('coords');
            myMap.balloon.open(this.coords, {
              contentHeader: 'Photo was taken here!',
              contentBody:
                '<p>Coordinates: ' + [
                  this.coords[0].toPrecision(6),
                  this.coords[1].toPrecision(6)
                ]
            });
          } else {
            myMap.balloon.close();
          }
        })
      })





    })
  },
  methods: {

  }
}
</script>

<style>
#map {
  width: 600px;
  height: 400px;
  margin-left: 35%;
}

.info_paragraph {
  font-size: 25px;
}
.add {
  color: blue;
  text-decoration: none;
}
</style>
